import { PureComponent, SyntheticEvent } from 'react'
import { BaseMenuElement, MenuElementListPanel, MenuElementListActions } from './menuitem'
import AppMuestreoMas from '../../proyecto__muestreo/AppMuestreoMas'
import AppMuestreoMum from '../../proyecto__muestreo/AppMuestreoMum'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal'
import { StatusDivBlock } from '../../global/StatusButtonAction'

declare var _3Creatives: any

type MenuItemMuestreoState = {
  items: ProyectoMuestreo[]
}

export class MenuItemMuestreo extends PureComponent<DefaultMenuElementProps<ProyectoMuestreo>, MenuItemMuestreoState> {
  state: MenuItemMuestreoState = {
    items: null
  }

  onMountPanel = () => {
    if (this.state.items === null) {
      const { id_paso, id_visita } = this.props.moduleParams
      $.getJSON(url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/muestreo/getJson`)).done((res) => {
        this.setState({
          items: _.isEmpty(res.response.muestreos) ? [] : this.parseItems(res.response.muestreos)
        })
      })
    }
  }

  onClickDeleteItem = (item: ProyectoMuestreo) => {
    bootbox.warningConfirm(
      `Desea eliminar este muestreo <strong>${item.name}</strong>?`,
      'No',
      'S, Eliminar',
      (r) => {
        if (!r) {
          return
        }

        const { id_paso, id_visita } = this.props.moduleParams

        $.blockUI(create_blockUI_loading_message('Eliminando muestreo'))
        $.ajax(url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/muestreo/${item.id}`), {
          data: { _csrf_token: _3Creatives.CSRF_TOKEN },
          type: 'delete'
        }).done((res) => {
          this.setState((prevState) => {
            const items = _.reject(prevState.items, { id: item.id })

            return { items }
          })

          $.unblockUI()
        })
      }
    )
  }

  parseItems(items: any[]) {
    return _.map(items, (item) => {
      item.configValues = JSON.parse(item.config)

      return item
    })
  }

  render() {
    const { id_paso, id_visita } = this.props.moduleParams

    return (
      <BaseMenuElement
        name='muestreo'
        counter={this.state.items === null ? this.props.counter : _.size(this.state.items)}
        icon='muestreo'
        title='Muestreo'
        open={this.props.open}
        onToggle={this.props.onOpen}
        onMountPanel={this.onMountPanel}
      >
        <div className='content'>
          {es_proyecto_editable() && (
            <div className='_a-center'>
              <a
                className='button ui right tiny compact plain green rounded'
                href={url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/muestreo/import`)}
                target='_blank'
              >
                Nuevo Muestreo <i className='icon plus' />
              </a>
            </div>
          )}

          <MuestreoList
            items={this.state.items}
            moduleParams={this.props.moduleParams}
            isEditable={es_proyecto_editable()}
            onClickDeleteItem={this.onClickDeleteItem}
          />
        </div>
      </BaseMenuElement>
    )
  }
}

class MuestreoList extends PureComponent<DefaultMenuItemListProps<ProyectoMuestreo>> {
  render() {
    if (this.props.items === null) {
      return (
        <div style={{ height: 300, position: 'relative' }}>
          <div className='ui active inverted text loader elastic olive'>Cargando Listado de Muestreos</div>
        </div>
      )
    } else if (_.size(this.props.items) == 0) {
      return <div className='empty nocontent'>0 Muestreos</div>
    } else {
      const { items } = this.props
      return (
        <MenuElementListPanel>
          {_.map(items, (item) => {
            return (
              <div key={item.id} className='listitem muestreo-listitem'>
                <MuestreoListItem
                  item={item}
                  moduleParams={this.props.moduleParams}
                  onClickDelete={this.props.onClickDeleteItem}
                />
              </div>
            )
          })}
        </MenuElementListPanel>
      )
    }
  }
}

type MuestreoListItemProps = {
  moduleParams: any
  item: ProyectoMuestreo
  onClickDelete: (item: ProyectoMuestreo) => void
}

class MuestreoListItem extends PureComponent<MuestreoListItemProps, { isModalOpen: boolean }> {
  state = {
    isModalOpen: false
  }

  getItemStatus() {
    const { item } = this.props
    switch (item.configValues.type) {
      case 'mas':
        return item.configValues.ficha_tecnica_mas_date ? 'COMPLETADO' : 'EN PROCESO'
      case 'mum':
        return item.configValues.ficha_tecnica_mum_date ? 'COMPLETADO' : 'EN PROCESO'
      default:
        return 'NUEVO'
    }
  }

  onClickDelete = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onClickDelete(this.props.item)
  }

  onClickItem = () => {
    const { item } = this.props

    if (!!item.configValues.type) {
      this.setState({ isModalOpen: true })
    }
  }

  renderModalContent() {
    const { item } = this.props

    switch (item.configValues.type) {
      case 'mas':
        return (
          <AppMuestreoMas
            data={item.configValues}
            id_visita={this.props.moduleParams.id_visita}
            id={this.props.item.id}
            editable={false}
          />
        )
      case 'mum':
        return (
          <AppMuestreoMum
            data={item.configValues}
            id_visita={this.props.moduleParams.id_visita}
            id={this.props.item.id}
            editable={false}
          />
        )
    }
  }

  render() {
    const { item, moduleParams } = this.props

    return (
      <div>
        <MenuElementListActions
          actions={{
            goto: {
              label: 'Ir al Muestreo',
              url: `proyecto/:id_proyecto/paso/${moduleParams.id_paso}/visita/${moduleParams.id_visita}/muestreo/${item.id}`
            },
            delete: { label: 'Eliminar', callback: this.onClickDelete, permission: 'muestreo_delete' }
          }}
        />
        <div>
          <div className='row r1'>
            <div className='pull-left r1_1'>
              <span className='title-h1'>
                {item.name}{' '}
                <small className='ui horizontal mini label'>
                  {(item.configValues.type ? item.configValues.type : 'SIN DEFINIR').toUpperCase()}
                </small>
                <StatusDivBlock
                  wrapperClassName='pull-right text-mini'
                  className='mini'
                  hideLabel
                  status={this.getItemStatus()}
                />
              </span>
            </div>
          </div>
          <div className='ui segment basic' style={{ marginTop: 0 }} onClick={this.onClickItem}>
            {!!item.configValues.type ? (
              <table className='ui very compact celled small table'>
                <thead>
                  <tr>
                    <th>Total Poblacin</th>
                    <th>Total Muestra</th>
                    <th>Error Tolerable</th>
                    <th>Error Proyectado</th>
                  </tr>
                </thead>
                <tbody>
                  <MuestreoListItemRow item={item} />
                </tbody>
              </table>
            ) : (
              <div className='_a-center'>
                <a
                  className='ui button orange'
                  href={url_for2(
                    `proyecto/:id_proyecto/paso/${moduleParams.id_paso}/visita/${moduleParams.id_visita}/muestreo/${item.id}`
                  )}
                >
                  Configurar Muestreo
                </a>
              </div>
            )}
          </div>
        </div>
        {this.state.isModalOpen && (
          <Modal
            dimmer='inverted'
            className='modal-muestreo modal-ab'
            closeIcon
            closeOnEscape
            open
            size='large'
            onClose={() => this.setState({ isModalOpen: false })}
          >
            <Modal.Header>Muestreo {item.name}</Modal.Header>
            <Modal.Content>{this.renderModalContent()}</Modal.Content>
          </Modal>
        )}
      </div>
    )
  }
}

const MuestreoListItemRow = (props: { item: ProyectoMuestreo }) => {
  const { item } = props

  const num = (string: string | number) => {
    const v = parseFloat('' + string)

    return isNaN(v) ? 0 : v
  }

  const getConfig = (config: string, defaultValue: number = null) => {
    if (typeof item.configValues[config] === 'undefined') {
      return defaultValue
    }

    return item.configValues[config]
  }

  const total_muestra = (() => {
    if (
      typeof item.configValues == 'undefined' ||
      typeof item.configValues.type == 'undefined' ||
      typeof item.configValues.muestras == 'undefined' ||
      typeof item.configValues.muestras[item.configValues.type] == 'undefined'
    ) {
      return 0
    }
    const muestras = item.configValues.muestras[item.configValues.type]

    return _.reduce(muestras, (acc: number, curr: any) => acc + num(curr.v), 0)
  })()

  switch (item.configValues.type) {
    case 'mas':
      const error_tolerable =
        getConfig('E', 5) == -1 ? item.configValues.Ev : getConfig('E', 5) * item.configValues.M / 100

      return (
        <tr>
          <td className='right aligned'>{accounting.formatNumber(item.configValues.M)}</td>

          <td className='right aligned'>{accounting.formatNumber(total_muestra)}</td>
          <td className='right aligned'>{accounting.formatNumber(num(error_tolerable))}</td>
          <td className='right aligned'>{accounting.formatNumber(num(item.configValues.pry_1))}</td>
        </tr>
      )
    case 'mum':
      return (
        <tr>
          <td className='right aligned'>{accounting.formatNumber(item.configValues.M)}</td>
          <td className='right aligned'>{accounting.formatNumber(total_muestra)}</td>

          <td className='right aligned'>{accounting.formatNumber(num(item.configValues.e2__v2))}</td>
          <td className='right aligned'>
            {accounting.formatNumber(num(Math.abs(item.configValues.total_error_proyectado)))}
          </td>
        </tr>
      )
  }
}
